.Login-page{
    display:flex;
    justify-content: center; 
    align-items: center;
}
.Login-box{ 
    margin-top: 8rem;
    width:350px ;
    height:300px; 
    background-color: rgb(26, 225, 205);
    border-radius: 10px;
} 
.Login-box:hover{ 
    background-color:yellowgreen;
} 
.Login-Header{
    display: flex; 
    justify-content: center;
    font-size:xx-large ;
    font-weight:bolder;
} 
.inputs{ 
    padding: 20px;
} 
.Name{ 
    width: 100%;
    height: 40px; 
    border-radius: 6px; 
    border-width: 1; 
    margin-bottom: 20px; 
} 
.Password{
    width: 100%;
    height: 40px;
    border-radius: 6px;
    border-width: 1; 
} 
.loginbutton{ 
    width: 100px;
    height: 30px; 
    background-color: white;
    border-radius: 6px;
    margin-top: 20px;
    margin-left: 35%;
    
    
} 
.loginbutton:hover{
    background-color:rgb(0, 68, 255);
    color: black;
} 
