.dashboard {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
  }
  
  main {
    flex: 1;
  }
  
  .navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    background-color: #f0f0f0;
  }
  
  .navbar-brand img {
    height: 3rem;
  }
  
  .navbar-profile img {
    height: 2rem;
    margin-right: 1rem;
    border-radius: 50%;
  }
  
  footer {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 4rem;
    background-color: #f0f0f0;
    margin-top: auto;
  }
  
  .footer-links {
    display: flex;
    align-items: center;
  }
  
  .footer-links a {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    margin-right: 1rem;
  }
  
  .footer-links img {
    height: 2rem;
    margin-right: 0.5rem;
  }
  
  .linkedin-logo {
    width: 2rem;
    height: 2rem;
    margin-right: 0.5rem;
  }
  .navbar{
    background-color: rgb(167, 218, 119);
  }
  